"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabaseClient";
import { useRouter } from "next/navigation";

type ContentIdea = {
  title?: string;
  hook?: string;
  script?: string;
  caption?: string;
  hashtags?: string[];
  cta?: string;
  explanation?: string;
};

export default function LibraryPage() {
  const router = useRouter();
  const [library, setLibrary] = useState<ContentIdea[]>([]);
  const [open, setOpen] = useState<number | null>(null);

  // âœ… AUTH PROTECTION
  useEffect(() => {
    supabase.auth.getUser().then(({ data }) => {
      if (!data.user) router.push("/login");
    });
  }, []);

  // Load from localStorage
  useEffect(() => {
    const raw = localStorage.getItem("cg_library_v1");
    if (raw) setLibrary(JSON.parse(raw));
  }, []);

  return (
    <div>
      <h1>Your Content Library</h1>

      {library.length === 0 && <p>No approved ideas yet.</p>}

      {library.map((idea, idx) => {
        const expanded = open === idx;

        return (
          <div
            key={idx}
            style={{ padding: 14, border: "1px solid #ddd", marginBottom: 10 }}
            onClick={() => setOpen(expanded ? null : idx)}
          >
            <b>{idea.title ?? `Idea ${idx + 1}`}</b>

            {expanded && (
              <div style={{ marginTop: 10 }}>
                <p><b>Hook:</b> {idea.hook}</p>
                <p><b>Caption:</b> {idea.caption}</p>
                <p><b>CTA:</b> {idea.cta}</p>
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
}
